ndizaje a través del juego")]),_c('p',[_vm._v("En la infancia, el juego no es solo una actividad recreativa, sino un vehículo fundamental para el aprendizaje. Mediante el juego, los niños desarrollan habilidades cognitivas, motoras y sociales. Por ejemplo, cuando un niño juega con bloques, no solo está aprendiendo sobre formas y tamaños, sino que también está desarrollando su capacidad para resolver problemas, trabajar en equipo y coordinar sus movimientos. Las actividades rectoras deben incluir juegos que estimulen estos aspectos del desarrollo infantil (Gonzales et al., 2021).")])]),_c('div',{staticClass:"col-md-8 col-lg-5 col-xl-4 order-1 mb-3 mb-lg-0"},[_c('figure',{attrs:{"data-aos":"zoom-in"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema3/4.png"),"alt":""}})])])])]),_c('div',{staticClass:"tabs-content border px-4 p-md-5 py-3 bg-color-8",attrs:{"titulo":"Promover un desarrollo integral","icon":require('@/assets/bullets/1.svg')}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-7 col-xl-8 mb-4 mb-md-0 order-2"},[_c('h4',[_vm._v("Promover un desarrollo integral")]),_c('p',[_vm._v("Las actividades rectoras, también deben promover un desarrollo integral, es decir, deben tener en cuenta todos los aspectos del crecimiento del niño: intelectual, emocional, social y físico. Un niño que participa en actividades que favorecen tanto su desarrollo cognitivo como el emocional, por ejemplo, será más capaz de manejar sus emociones, interactuar de manera positiva con otros niños y desarrollar habilidades de comunicación. Es crucial que las actividades rectoras proporcionen un balance entre estos diferentes tipos de aprendizaje (Mérida & Ramírez, 2015).")])]),_c('div',{staticClass:"col-md-8 col-lg-5 col-xl-4 order-1 mb-3 mb-lg-0"},[_c('figure',{attrs:{"data-aos":"zoom-in"}},[_c('img',{staticStyle:{"width":"100%"},attrs:{"src":require("@/assets/curso/tema3/5.png"),"alt":""}})])])])]),_c('div',{staticClass:"tabs-content border px-4 p-md-